class App extends React.Component {
  constructor(props) {
    super(props);

    // set cookie of user id
    const cookies = new Cookies();
    let userID = cookies.get("userID");
    // if no user id, generate one
    if (userID === undefined) {
      userID = uuidToHex(uuid4());
      cookies.set('userID', userID, {
        path: '/',
        maxAge: (60 * 60 * 24) // one day
      });
      console.log("<App> detected new user")
      console.log("<App> assigned uid: " + userID)
    } else {
      console.log("<App> detected uid: " + userID)
    }
    window.userID = userID;
    console.log("<App> logged in as: %c" + window.userID, "background: yellow");

    // access local storage
    let server = window.localStorage.getItem("server");
    if (server == null) {
      server = "http://localhost:5000/";
      window.localStorage.setItem("server", server);
    }

    // init this app
    window.server = server;
    this.state = {
      showSettings: false,
      server: server,
    };

    // prevent leave/reload page
    // window.onbeforeunload = () => { return true; };
  }

  render() {
    return (
      <div className="App">

        {/* Header */}
        <div className="Header">
          <Navbar className="shadow" bg="dark" variant="dark" sticky="top" style={{ height: "50px" }}>
            {/* logo 1 */}
            <Navbar.Brand
              className="pl-2 font-weight-bold"
              style={{ cursor: "help" }}
              title="Table to Wikidata Mapping Language"
            >T2WML&nbsp;GUI</Navbar.Brand>

            {/* logo 2 */}
            {/* <Navbar.Brand className="pl-2">
              <span className="font-weight-bold">T</span>able&nbsp;
              <span className="font-weight-bold">2</span>&nbsp;
              <span className="font-weight-bold">W</span>ikidata&nbsp;
              <span className="font-weight-bold">M</span>apping&nbsp;
              <span className="font-weight-bold">L</span>anguage&nbsp;
              <span className="font-weight-bold">GUI</span>
            </Navbar.Brand> */}

            <Navbar.Text className="pr-2">v1.0&nbsp;(beta)</Navbar.Text>

            {/* top right of page */}
            <Navbar.Text className="pr-2 ml-auto" style={{ verticalAlign: "center" }}>
              {/* <span className="font-weight-bold">User ID:&nbsp;</span>
              <span className="pr-2">{window.userID}</span> */}
              <Button
                className="d-inline-block float-right"
                variant="outline-light"
                size="sm"
                style={{ padding: "0rem 0.5rem" }}
                onClick={this.handleShowSettings.bind(this)}
              >Settings</Button>
            </Navbar.Text>

          </Navbar>
        </div>

        {/* Settings */}
        <Modal show={this.state.showSettings} size="lg" onHide={() => { }}>
          <Modal.Header style={{ background: "whitesmoke" }}>
            <Modal.Title>Settings</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form className="container">
              <Form.Group as={Row} controlId="settingsUserID" style={{ marginTop: "1rem" }}>
                <Form.Label column sm="12" md="2" className="text-right">User&nbsp;ID</Form.Label>
                <Col sm="12" md="10">
                  <Form.Control type="text" defaultValue={window.userID} readOnly />
                </Col>
              </Form.Group>
              <Form.Group as={Row} controlId="settingsServer" onChange={(event) => this.setState({ server: event.target.value })}>
                <Form.Label column sm="12" md="2" className="text-right">Server</Form.Label>
                <Col sm="12" md="10">
                  <Form.Control type="url" defaultValue={window.server} />
                </Col>
              </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer style={{ background: "whitesmoke" }}>
            <Button variant="outline-dark" onClick={this.handleCloseSettings.bind(this)}>
              Cancel
            </Button>
            <Button variant="dark" onClick={this.handleSaveSettings.bind(this)}>
              Save
            </Button>
          </Modal.Footer>
        </Modal>

        {/* Content */}
        <div className="Content">

          {/* original */}
          <SplitPane className="p-3" split="vertical" defaultSize="60%" minSize={200} maxSize={-300} style={{ height: "calc(100vh - 50px)", background: "#f8f9fa" }}>
            <TableViewer />
            <SplitPane className="" split="horizontal" defaultSize="60%" minSize={200} maxSize={-200}>
              <YAMLEditor />
              <Output />
            </SplitPane>
          </SplitPane>

          {/* for paper purpose only */}
          {/* <SplitPane className="p-3" split="vertical" defaultSize="60%" minSize={200} maxSize={-300} style={{ height: "calc(100vh - 50px)", background: "#f8f9fa" }}>
            <SplitPane className="" split="horizontal" defaultSize="60%" minSize={100} maxSize={-200}>
              <TableViewer />
              <Output />
            </SplitPane>
            <YAMLEditor />
          </SplitPane> */}

          {/* colorization 
          window.TableViewer.updateStyleByRegion("A", "5", "A", "7", {"background-color":"hsl(20,100%,80%)"});
          window.TableViewer.updateStyleByRegion("B", "5", "B", "7", {"background-color":"hsl(200,100%,80%)"});
          window.TableViewer.updateStyleByRegion("C", "3", "H", "4", {"background-color":"hsl(200,100%,80%)"});
          window.TableViewer.updateStyleByRegion("C", "5", "H", "7", {"background-color":"hsl(150,50%,80%)"});
          */}

        </div>
      </div >
    );
  }

  handleShowSettings() {
    this.setState({ showSettings: true });
  }

  handleCloseSettings() {
    this.setState({ showSettings: false });
  }

  handleSaveSettings(event) {
    console.log("<App> updated settings");
    window.server = this.state.server;
    window.localStorage.setItem("server", this.state.server);
    this.setState({ showSettings: false });
  }
}

