class YAMLEditor extends React.Component {
  constructor(props) {
    super(props);
    // window.YAMLEditor = this;
    this.state = {
      textYAML: "# type your YAML code here...",
      jsonYAML: null,
      isValidYAML: true,
      errMsg: "",
      errStack: ""
    };
    this.handleOpenFile = this.handleOpenFile.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleApply = this.handleApply.bind(this);
  }

  handleOpenFile(event) {
    const file = event.target.files[0];
    if (!file) return;
    console.log("<YAMLEditor> opened file: " + file.name);

    // upload local yaml
    let reader = new FileReader();
    reader.readAsText(file);
    reader.onloadend = (() => {
      let textYAML = reader.result;
      this.setState({ textYAML: textYAML });
      try {
        let jsonYAML = yaml.safeLoad(textYAML);
        // console.log(jsonYAML);
        this.setState({
          jsonYAML: jsonYAML,
          isValidYAML: true,
          errMsg: null,
          errStack: null
        });
      } catch (err) {
        // console.log(err);
        this.setState({
          isValidYAML: false,
          errMsg: "⚠️ " + err.message.match(/[^:]*(?=:)/)[0],
          errStack: err.stack
        });
      }
    });
  }

  handleChange(event) {
    // console.log("<YAMLEditor> was changed.");
    const textYAML = this.refs.monaco.editor.getModel().getValue();
    this.setState({ textYAML: textYAML });
    try {
      let jsonYAML = yaml.safeLoad(textYAML);
      // console.log(jsonYAML);
      this.setState({
        jsonYAML: jsonYAML,
        isValidYAML: true,
        errMsg: null,
        errStack: null
      });
    } catch (err) {
      // console.log(err);
      this.setState({
        isValidYAML: false,
        errMsg: "⚠️ " + err.message.match(/[^:]*(?=:)/)[0],
        errStack: err.stack
      });
    }
  }

  handleApply(event) {
    console.log("<YAMLEditor> clicked apply");

    // fetch remote json for highlight
    const uploadYamlURL = window.server + "upload_yaml?id=" + window.userID;

    let formData = new FormData();
    formData.append("yaml", this.state.textYAML);

    fetch(uploadYamlURL, {
      body: formData,
      credentials: "same-origin",
      // credentials: "include",
      method: "POST"
    }).then((response) => {
      return response.json();
    }).then((json) => {
      console.log("<YAMLEditor> fetched remote file: %cupload_yaml.json", "background: yellow");
      console.log(json);
      window.TableViewer.updateStyleByDict(json);
      window.Output.setState({ downloadDisabled: false });
    });
  }

  render() {
    const textYAML = this.state.textYAML;
    return (
      <div className="w-100 h-100 p-1">
        <Card className="w-100 h-100 shadow-sm">

          {/* Title of YAML-Editor */}
          <Card.Header style={{ height: "40px", padding: "0.5rem 1rem", background: "#006699" }}>
            <div
              className="text-white font-weight-bold d-inline-block text-truncate"
              style={{ width: "calc(100% - 60px)" }}
            >YAML&nbsp;Editor</div>
            <OverlayTrigger
              placement="bottom"
              trigger="hover"
              // defaultShow="true"
              overlay={
                <Tooltip id="tooltip-open-yaml">
                  <div className="text-left small">
                    <b>Accepted file types:</b>
                    <br />• YAML Ain't Markup Language (.yaml)
                      </div>
                </Tooltip>
              }
            >
              <Button
                className="d-inline-block float-right"
                variant="outline-light"
                size="sm"
                style={{ padding: "0rem 0.5rem" }}
                onClick={function () {
                  // Call the hidden file input tag
                  document.getElementById("file_yaml").click();
                }}
              >Open</Button>
            </OverlayTrigger>
            <input
              type="file"
              id="file_yaml"
              accept=".yaml"
              style={{ display: "none" }}
              onChange={this.handleOpenFile}
              onClick={(event) => { event.target.value = null }}
            />
          </Card.Header>

          {/* Content of YAML-Editor */}
          <Card.Body className="w-100 h-100 p-0" id="YAML-Editor" style={{ overflow: "scroll" }}>
            <MonacoEditor
              ref="monaco"
              width="100%"
              height="100%"
              language="yaml"
              theme="vs"
              value={textYAML}
              options={{
                // All options for construction of monaco editor:
                // https://microsoft.github.io/monaco-editor/api/interfaces/monaco.editor.ieditorconstructionoptions.html
                automaticLayout: true,
                lineNumbersMinChars: 4,
                // minimap: { enabled: false, },
                // mouseWheelZoom: true,
                renderLineHighlight: "all", // "none" | "gutter" | "line" | "all"
                renderWhitespace: "all", // "none" | "boundary" | "all"
                scrollbar: {
                  horizontalScrollbarSize: 10,
                  horizontalSliderSize: 6,
                  verticalScrollbarSize: 10,
                  verticalSliderSize: 6
                },
                showFoldingControls: 'always',
              }}
              onChange={this.handleChange}
              editorDidMount={(editor, monaco) => {
                editor.getModel().updateOptions({ tabSize: 2 });
              }}
            />
          </Card.Body>

          {/* apply buttom */}
          <Card.Footer style={{ height: "40px", padding: "0.5rem 1rem", background: "whitesmoke" }}>
            <div
              className="d-inline-block text-truncate"
              style={{
                // fontFamily: "Menlo, Monaco, \"Courier New\", monospace",
                fontSize: "14px",
                color: "#990000",
                width: "calc(100% - 60px)",
                cursor: "help"
              }}
              title={this.state.errStack}
            >{this.state.errMsg}</div>
            <Button
              className="d-inline-block float-right"
              size="sm"
              style={{ borderColor: "#006699", background: "#006699", padding: "0rem 0.5rem" }}
              onClick={this.handleApply.bind(this)}
              disabled={!this.state.isValidYAML}
            >Apply</Button>
          </Card.Footer>
        </Card>
      </div>
    );
  }
}

