class Output extends React.Component {
  constructor(props) {
    super(props);
    window.Output = this;
    this.state = {
      // itemID: itemID,
      // itemName: itemName,
      // itemCol: itemCol,
      // itemRow: itemRow,
      // propertyID: propertyName,
      // value: value,
      // qualifiers: qualifiers,
      cache: {}, // cache for Wikidata queries
      showDownload: false,
      downloadDisabled: true,
      downloadFileName: window.userID,
      downloadFileType: "json",
    };
  }

  removeBorders() {
    // remove current cell border
    let col = this.state.currCol;
    let row = this.state.currRow;
    if (col !== undefined && row !== undefined) {
      window.TableViewer.updateStyleByCell(col, row, { "border": "" });
    }

    // remove item border
    col = this.state.itemCol;
    row = this.state.itemRow;
    if (col !== undefined && row !== undefined) {
      window.TableViewer.updateStyleByCell(col, row, { "border": "" });
    }

    // remove qualifiers borders
    let qualifiers = this.state.qualifiers;
    if (qualifiers !== undefined && qualifiers != null) {
      for (let i = 0, len = qualifiers.length; i < len; i++) {
        col = qualifiers[i]["col"];
        row = qualifiers[i]["row"];
        window.TableViewer.updateStyleByCell(col, row, { "border": "" });
      }
    }
  }

  clearOutput() {
    this.setState({
      itemID: null,
      itemName: null,
      itemCol: null,
      itemRow: null,
      propertyID: null,
      value: null,
      qualifiers: null,
    });
  }

  getHueByRandom(total) {
    // hue in [0, 359]
    const division = Math.floor(360 / total);
    return Math.floor(Math.random() * total) * division;
  }

  getHueByString(total, str) {
    // hue in [0, 359]
    const i = parseInt(str.substring(1)) % total;
    const division = Math.floor(360 / total);
    return i * division;
  }

  json2state(colName, rowName, json) {
    // remove previous borders
    this.removeBorders();

    if (json["statement"] === undefined) {
      this.clearOutput();
      return;
    }

    // retrieve cache
    let cache = this.state.cache;

    // item
    const itemID = json["statement"]["item"];
    // const itemName = window.TableViewer.state.rowData[row][col];
    if (cache[itemID] === undefined) {
      this.setState({ itemID: itemID });
      this.queryWikidata(itemID, "itemName");
    } else {
      this.setState({ itemID: itemID, itemName: cache[itemID] });
    }
    let [col, row] = json["statement"]["item_cell_index"].match(/[a-z]+|[^a-z]+/gi);
    window.TableViewer.updateStyleByCell(col, row, { "border": "1px solid black" });
    this.setState({ itemCol: col, itemRow: row });

    // property
    const propertyID = json["statement"]["property"];
    if (cache[propertyID] === undefined) {
      this.setState({ propertyID: propertyID });
      this.queryWikidata(propertyID, "propertyName");
    } else {
      this.setState({ propertyID: propertyID, propertyName: cache[propertyID] });
    }

    // value
    const value = json["statement"]["value"];
    this.setState({ value: value });
    window.TableViewer.updateStyleByCell(colName, rowName, { "border": "1px solid hsl(150, 50%, 40%)" });
    this.setState({ currCol: colName, currRow: rowName });

    // qualifiers
    let temp = json["statement"]["qualifier"];
    let qualifiers = [];
    for (let i = 0, len = temp.length; i < len; i++) {
      let qualifier = {};

      qualifier["propertyID"] = temp[i]["property"];
      if (cache[qualifier["propertyID"]] === undefined) {
        this.queryWikidata(qualifier["propertyID"], "qualifiers", i, "propertyName");
      } else {
        qualifier["propertyName"] = cache[qualifier["propertyID"]];
      }

      qualifier["valueName"] = temp[i]["value"];
      if (/^[PQ]\d+$/.test(qualifier["valueName"])) {
        if (cache[qualifier["valueName"]] === undefined) {
          this.queryWikidata(qualifier["valueName"], "qualifiers", i, "valueName");
        } else {
          qualifier["valueID"] = qualifier["valueName"];
          qualifier["valueName"] = cache[qualifier["valueName"]];
        }
      }

      [col, row] = temp[i]["cell_index"].match(/[a-z]+|[^a-z]+/gi);
      qualifier["col"] = col;
      qualifier["row"] = row;
      // let hue = this.getHueByRandom(10); // first param is the total number of colors
      let hue = this.getHueByString(10, qualifier["propertyID"]);
      window.TableViewer.updateStyleByCell(col, row, { "border": "1px solid hsl(" + hue + ", 100%, 40%)" });

      qualifiers.push(qualifier);
    }
    this.setState({ qualifiers: qualifiers });
  }

  queryWikidata(node, field, index = 0, subfield = "propertyName") {
    // TODO: use <local stroage> to store previous query result even longer
    const api = "https://query.wikidata.org/sparql?format=json&query=SELECT%20DISTINCT%20%2a%20WHERE%20%7B%0A%20%20wd%3A" + node + "%20rdfs%3Alabel%20%3Flabel%20.%20%0A%20%20FILTER%20%28langMatches%28%20lang%28%3Flabel%29%2C%20%22EN%22%20%29%20%29%20%20%0A%7D%0ALIMIT%201";
    fetch(api)
      .then(response => response.json())
      .then(json => {
        const name = json["results"]["bindings"][0]["label"]["value"];
        if (field === "itemName") {
          this.setState({ itemName: name });
        } else if (field === "propertyName") {
          this.setState({ propertyName: name });
        } else if (field === "qualifiers") {
          let qualifiers = this.state.qualifiers;
          if (subfield === "propertyName") {
            qualifiers[index]["propertyName"] = name;
          } else if (subfield === "valueName") {
            qualifiers[index]["valueID"] = qualifiers[index]["valueName"];
            qualifiers[index]["valueName"] = name;
          }
          this.setState({ qualifiers: qualifiers });
        }
        let cache = this.state.cache;
        cache[node] = name;
        this.setState({ cache: cache });
      });
  }

  render() {
    let outputDiv = [];
    let itemName = this.state.itemName;
    if (itemName) {

      // item
      let itemID = this.state.itemID;
      let itemIDDiv = (
        <a
          href={"https://www.wikidata.org/wiki/" + itemID}
          target="_blank"
          rel="noopener noreferrer"
          style={{ "color": "hsl(200, 100%, 30%)" }}
        >{itemID}</a>
      );

      // property
      let propertyDiv;
      let propertyID = this.state.propertyID;
      let propertyName = this.state.propertyName;
      if (propertyName) {
        propertyDiv =
          <span key="property">
            <a
              href={"https://www.wikidata.org/wiki/Property:" + propertyID}
              target="_blank"
              rel="noopener noreferrer"
              style={{ "color": "hsl(200, 100%, 30%)" }}
            >{propertyName}</a>
          </span>
          ;
      } else {
        propertyDiv =
          <span key="property">
            <a
              href={"https://www.wikidata.org/wiki/Property:" + propertyID}
              target="_blank"
              rel="noopener noreferrer"
              style={{ "color": "hsl(200, 100%, 30%)" }}
            >{propertyID}</a>
          </span>
          ;
      }

      // value
      let valueDiv = this.state.value;

      // qualifiers
      let qualifiersDiv = [];
      let qualifiers = this.state.qualifiers;
      if (qualifiers) {
        for (let i = 0, len = qualifiers.length; i < len; i++) {
          let qualifier = qualifiers[i];

          // qualifier property
          let qualifierPropertyDiv;
          let qualifierPropertyID = qualifier["propertyID"];
          let qualifierPropertyName = qualifier["propertyName"];
          if (qualifierPropertyName) {
            qualifierPropertyDiv =
              <a
                href={"https://www.wikidata.org/wiki/Property:" + qualifierPropertyID}
                target="_blank"
                rel="noopener noreferrer"
                style={{ "color": "hsl(200, 100%, 30%)" }}
                key="qualifierProperty"
              >{qualifierPropertyName}</a>
              ;
          } else {
            qualifierPropertyDiv =
              <a
                href={"https://www.wikidata.org/wiki/Property:" + qualifierPropertyID}
                target="_blank"
                rel="noopener noreferrer"
                style={{ "color": "hsl(200, 100%, 30%)" }}
                key="qualifierProperty"
              >{qualifierPropertyID}</a>
              ;
          }

          // qualifier value
          let qualifierValueDiv;
          let qualifierValueID = qualifier["valueID"];
          let qualifierValueName = qualifier["valueName"];
          if (qualifierValueID) {
            qualifierValueDiv =
              <a
                href={"https://www.wikidata.org/wiki/" + qualifierValueID}
                target="_blank"
                rel="noopener noreferrer"
                style={{ "color": "hsl(200, 100%, 30%)" }}
                key="qualifierValue"
              >{qualifierValueName}</a>
              ;
          } else {
            qualifierValueDiv = qualifierValueName;
          }

          // append to qualifiersDiv
          qualifiersDiv.push(
            <div key={i}>- {qualifierPropertyDiv}: {qualifierValueDiv}</div>
          );
        }
      }

      // final output
      outputDiv.push(
        <Card.Title key="item">
          <span style={{ fontSize: "24px", fontWeight: "bolder" }}>
            {itemName}
          </span>
          &nbsp;
          <span style={{ fontSize: "20px" }}>
            ({itemIDDiv})
          </span>
        </Card.Title>
      );
      outputDiv.push(
        <table className="w-100" key="outputTable" style={{ borderCollapse: "collapse" }}>
          <tbody>
            <tr style={{ borderTop: "1px solid lightgray", borderBottom: "1px solid lightgray" }}>
              <td className="p-2" style={{ fontSize: "16px", fontWeight: "bold", verticalAlign: "top", width: "40%" }}>
                {propertyDiv}
              </td>
              <td className="p-2">
                <div style={{ fontSize: "16px", fontWeight: "bold" }}>
                  {valueDiv}
                </div>
                <div style={{ fontSize: "14px" }}>
                  {qualifiersDiv}
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      );
    }

    return (
      <>
        <div className="w-100 h-100 p-1">
          <Card className="w-100 h-100 shadow-sm">

            {/* Title of Output */}
            <Card.Header style={{ height: "40px", padding: "0.5rem 1rem", background: "#990000" }}>
              <div
                className="text-white font-weight-bold d-inline-block text-truncate"
                style={{ width: "calc(100% - 90px)" }}
              >Output</div>
              <Button
                className="d-inline-block float-right"
                variant="outline-light"
                size="sm"
                style={{ padding: "0rem 0.5rem" }}
                onClick={this.handleShowDownload.bind(this)}
                disabled={this.state.downloadDisabled}
              >Download</Button>
            </Card.Header>

            {/* Content of Output */}
            <Card.Body className="p-3" id="Output" style={{ width: "100%", height: "1px", overflow: "scroll" }}>
              {outputDiv}
            </Card.Body>
          </Card>
        </div>

        <Modal show={this.state.showDownload} onHide={() => { }}>
          <Modal.Header style={{ background: "whitesmoke" }}>
            <Modal.Title>Download</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form className="container">
              <Form.Group as={Row} style={{ marginTop: "1rem" }}>
                <Col md="9" className="pr-0">
                  <Form.Control
                    type="text"
                    defaultValue={this.state.downloadFileName}
                    onChange={(event) => this.setState({ downloadFileName: event.target.value })}
                  />
                </Col>
                <Col md="3" className="pl-0">
                  <Form.Control as="select" onChange={(event) => this.setState({ downloadFileType: event.target.value })}>
                    <option value="json">.json</option>
                    <option value="ttl">.ttl</option>
                  </Form.Control>
                </Col>
              </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer style={{ background: "whitesmoke" }}>
            <Button variant="outline-dark" onClick={this.handleCloseDownload.bind(this)}>
              Cancel
            </Button>
            <OverlayTrigger
              placement="bottom"
              trigger="hover"
              // defaultShow="true"
              overlay={
                <Tooltip>
                  <div className="text-left small">
                    Your file will be prepared shortly. Once the file is ready, it will be downloaded automatically.
                  </div>
                </Tooltip>
              }
            >
              <Button variant="dark" onClick={this.handleStartDownload.bind(this)}>
                Start
              </Button>
            </OverlayTrigger>

          </Modal.Footer>
        </Modal>
      </>
    );
  }

  handleShowDownload() {
    this.setState({ showDownload: true });
  }

  handleCloseDownload() {
    this.setState({ showDownload: false });
  }

  handleStartDownload(event) {
    const filename = this.state.downloadFileName + "." + this.state.downloadFileType;
    console.log("<Output> sent download request for: %c" + filename, "background: yellow");

    const downloadAPI = window.server + "download?id=" + window.userID + "&type=" + this.state.downloadFileType;
    fetch(downloadAPI, {
      credentials: "same-origin",
      // credentials: "include",
      method: "POST"
    }).then((response) => {
      if (!response.ok) {
        throw Error(response.statusText);
      }
      return response;
    }).then((response) => {
      return response.json();
    }).then((text) => {
      console.log("<Output> fetched requested file, download will start soon");
      console.log(text);
      Downloader(text, filename);
    }).catch((error) => {
      // console.log(error);
    });

    this.setState({ showDownload: false });
  }
}

